nav {
  height: 6rem;
  width: 100vw;
  background-color: #131418;
  box-shadow: 0 3px 20px rgba(0, 0, 0, 0.2);
  display: flex;
  position: fixed;
  z-index: 1000;
  transition: 0.3s;
}

/*Styling logo*/
.Navbar_logo__3q0Vt {
  padding: 1vh 1vw;
  text-align: center;
}
.Navbar_logo__3q0Vt img {
  height: 5rem;
  width: 5rem;
}

/*Styling Links*/
.Navbar_navLinks__1045q {
  display: flex;
  list-style: none;
  width: 88vw;
  padding: 0 0.7vw;
  justify-content: space-evenly;
  align-items: center;
  text-transform: uppercase;
}
.Navbar_navLinks__1045q li a {
  color: inherit;
  text-decoration: none;
  margin: 0 0.7vw;
}
/* .navLinks li a:hover {
  color: #107e0c;
} */
.Navbar_navLinks__1045q li {
  position: relative;
}
.Navbar_navLinks__1045q li a::before {
  content: "";
  display: block;
  height: 3px;
  width: 0%;
  background-color: #f2f5f7;
  position: absolute;
  top: 24px;
  transition: all ease-in-out 0.3s;
  margin: 0 0 0 9%;
}

.Navbar_navLinks__1045q li a:hover::before {
  width: 80%;
}

/*Styling Hamburger Icon*/
.Navbar_hamburger__21rIQ div {
  width: 30px;
  height: 3px;
  background: black;
  margin: 5px;
  transition: all 0.3s ease;
}
.Navbar_hamburger__21rIQ {
  display: none;
}

/*Stying for small screens*/
@media screen and (max-width: 800px) {
  nav {
    position: fixed;
    z-index: 3;
  }
  .Navbar_hamburger__21rIQ {
    display: block;
    position: absolute;
    cursor: pointer;
    right: 5%;
    top: 50%;
    -webkit-transform: translate(-5%, -50%);
            transform: translate(-5%, -50%);
    z-index: 2;
    transition: all 0.7s ease;
  }
  .Navbar_navLinks__1045q {
    position: fixed;
    /* background: #131418; */
    background: #fff;
    height: 100vh;
    z-index: 1000;
    width: 100%;
    flex-direction: column;
    clip-path: circle(50px at 90% -20%);
    -webkit-clip-path: circle(50px at 90% -10%);
    transition: all 1s ease-out;
    pointer-events: none;
  }
  .Navbar_navLinks__1045q.Navbar_open__3N5Ok {
    clip-path: circle(1000px at 90% -10%);
    -webkit-clip-path: circle(1000px at 90% -10%);
    pointer-events: all;
  }
  .Navbar_navLinks__1045q li {
    opacity: 0;
  }
  .Navbar_navLinks__1045q li:nth-child(1) {
    transition: all 0.5s ease 0.2s;
  }
  .Navbar_navLinks__1045q li:nth-child(2) {
    transition: all 0.5s ease 0.4s;
  }
  .Navbar_navLinks__1045q li:nth-child(3) {
    transition: all 0.5s ease 0.6s;
  }
  .Navbar_navLinks__1045q li:nth-child(4) {
    transition: all 0.5s ease 0.7s;
  }
  .Navbar_navLinks__1045q li:nth-child(5) {
    transition: all 0.5s ease 0.8s;
  }
  .Navbar_navLinks__1045q li:nth-child(6) {
    transition: all 0.5s ease 0.9s;
    margin: 0;
  }
  .Navbar_navLinks__1045q li:nth-child(7) {
    transition: all 0.5s ease 1s;
    margin: 0;
  }
  li.Navbar_fade__3zcJs {
    opacity: 1;
  }
}
/*Animating Hamburger Icon on Click*/
